import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export function showmessage(msg, autoClose = 3000, msgType = 'success') {

    if (msgType === 'success') {
        toast.success(msg, {
            position: "bottom-center",
            autoClose: autoClose,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: false,
            progress: undefined,
        });
    } else {
        toast.error(msg, {
            position: "bottom-center",
            autoClose: autoClose,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: false,
            draggable: false,
            progress: undefined,
        });
    }
}

